import { Typography } from "@mui/joy"
import CenteredModal from "./CenteredModal"

export default function EducationModal({open, onClose, name, type, profile, startDate, endDate, description}) {
    return (
        <CenteredModal
            open={open}
            onClose={onClose}
        >
            <Typography level="title-md">{name}</Typography>
            <Typography level="body-md"><strong> :</strong> {type}</Typography>
            <Typography level="body-md"><strong>/:</strong> {profile}</Typography>
            <Typography level="body-md"><strong> :</strong> {startDate} - {endDate}</Typography>
            <Typography level="body-md">{description}</Typography>
        </CenteredModal>
    )
}